.contacts-form {
  margin-top: 40px;
}

textarea {
  width: 100%;
  height: 199px;
  left: 20px;
  border: 1px solid #487996;
  border-radius: 50px 0px;
  margin-bottom: 4px;
  margin-top: 4px;
  resize: none;

}
.textarea-wrap{
  position: relative;
}
.text-btn{
  position: absolute;
  display: flex;
  top: 10px;
}
.contact-field {
  margin-bottom: 44px;
}
.contact-field.text {
  margin-bottom: 0;
}
.contact-input {
  width: 100%;
  height: 44px;
  border: transparent;
  border-bottom: 1px solid #487996;
  padding: 0;
  
}

.contact-item.text {
  display: block;
}
.contact-text::placeholder {
  padding-left: 22px;
  padding-top: 20px;
}
.btn-form {
  margin-top: 40px;
  margin-bottom: 60px;
}
.hint{
  font-weight: 400;
font-size: 16px;
line-height: 1.25;
color: #CDCDCD;
}

.contact-item{
  font-weight: 400;
font-size: 16px;
line-height: 1.25;
text-transform: capitalize;
color: #2B2B2B;
}


